.template-about-page-container {
  .hero-section {
    position: relative;
    background-color: $orange;
    padding-top: 100px;
    padding-bottom: 70px;

    @media #{$tablet-s} {
      padding-top: max(px-to-vw(200), 150px);
      padding-bottom: max(px-to-vw(111), 80px);
    }

    .section-content {
      display: flex;
      flex-wrap: wrap;
      align-items: center;

      .left {
        width: 100%;
        margin-bottom: 40px;

        @media #{$tablet-s} {
          width: 50%;
          margin-bottom: 0;
        }

        .image-holder {
          position: relative;
          padding-top: 90.2%;
          border-radius: max(px-to-vw(40), 20px);
          overflow: hidden;

          img {
            @include cropped-img();
          }
        }
      }

      .right {
        width: 100%;
        box-sizing: border-box;

        @media #{$tablet-s} {
          width: 50%;
          padding-left: px-to-vw(80);
        }

        @media #{$laptop-s} {
          padding-left: px-to-vw(186);
        }

        .small-title {
          @include small-title();
          color: #fff;
          margin-bottom: max(px-to-vw(8), 13px);
        }

        .title {
          @include small-editor-styles();
          @include title-3();
          margin-bottom: max(px-to-vw(35), 30px);

          span {
            color: $yellow;
          }
        }

        .description {
          @include text-1();
          color: #fff;

          @media #{$laptop-s} {
            padding-right: px-to-vw(75);
          }
        }
      }
    }
  }

  .blocks-section {
    .block {
      background-color: $grey-blue;
      padding: max(#{px-to-vw(140)}, 55px) 0 max(#{px-to-vw(90)}, 55px);

      &.theme-1 {
        background-color: $grey-blue;
      }

      &.theme-2 {
        background-color: #b2cad9;

        .block-title {
          color: $grey-blue;
        }

        .block-info {
          border-color: $grey-blue;

          .block-info-title {
            h3 {
              color: $grey-blue;
            }
          }

          .block-info-description {
            .read-more-btn {
              color: $grey-blue;
            }

            p {
              color: $grey-blue;
            }
          }
        }
      }

      .block-hero {
        border-radius: max(#{px-to-vw(40)}, 20px);
        overflow: hidden;
        min-height: max(41.2vw, 387px);
        height: 100%;
        position: relative;
        display: flex;
        // align-items: center;
        justify-content: center;
        margin-bottom: max(#{px-to-vw(70)}, 43px);
        // padding: 50px 0;
        box-sizing: border-box;
        background-color: $orange;

        img {
          z-index: 0;
          width: 100%;
          height: auto;
          object-fit: cover;
        }

        .block-title {
          @include title-2();
          padding: 0 20px;
          box-sizing: border-box;
          text-align: center;
          z-index: 2;
          position: absolute;
          height: 100%;
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          &:after {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            background-color: rgba(#000, 25%);
            z-index: -1;
          }
          @media #{$tablet-s} {
            padding: 0 px-to-vw(200);
          }
        }
      }

      .block-info {
        display: flex;
        flex-wrap: wrap;
        border-top: solid 1px #fff;
        border-bottom: solid 1px #fff;
        padding: max(px-to-vw(60), 22px) 0;

        .block-info-title {
          width: 100%;
          box-sizing: border-box;

          @media #{$tablet-s} {
            width: 44%;
            padding-right: px-to-vw(100);
          }

          @media #{$laptop-s} {
            padding-right: px-to-vw(197);
          }

          h3 {
            @include title-4();
          }
        }

        .block-info-description {
          @include small-editor-styles();
          width: 100%;

          @media #{$tablet-s} {
            width: 56%;
          }

          .read-more-btn {
            color: #fff;
            margin-top: 15px;

            @media #{$tablet-s} {
              display: none;
            }

            &.active {
              span {
                transform: rotate(180deg);
              }
            }
          }

          p {
            @include text-2();
            color: #fff;
            display: none;
            margin-top: 30px;

            @media #{$tablet-s} {
              display: block !important; // overwrite js slideToggle if someone resized screen from mobile to desktop
              margin-top: 0;
            }
          }
        }
      }

      .template-part-cards-2 {
        margin-top: max(#{px-to-vw(50)}, 55px);
      }
    }
  }

  .slide-section {
    background-color: $orange;
    padding: max(px-to-vw(145), 55px) 0 max(px-to-vw(85), 46px);

    .section-header {
      margin-bottom: max(px-to-vw(100), 47px);

      .title {
        @include title-3();
        color: #fff;
        text-align: center;
      }
    }

    .slider-holder {
      display: flex;
      flex-wrap: wrap;
      align-items: center;

      .left {
        width: 100%;
        position: relative;
        border-radius: max(px-to-vw(40), 20px);
        overflow: hidden;
        margin-bottom: 33px;

        @media #{$tablet-l} {
          width: 52%;
          margin-bottom: 0;
        }

        .swiper {
          z-index: 1;

          .swiper-slide {
            .image-holder {
              position: relative;
              padding-top: 73.7%;

              img {
                @include cropped-img();
              }
            }

            .btn {
              position: absolute;
              bottom: 14px;
              left: 50%;
              transform: translateX(-50%);
              width: max(px-to-vw(235), 145px);

              @media #{$tablet-s} {
                bottom: px-to-vw(28);
              }

              &:hover {
                svg {
                  path {
                    fill: #fff;
                  }
                }
              }

              svg {
                margin-right: max(px-to-vw(10), 5px);
                height: auto;
                width: max(px-to-vw(13), 11px);

                path {
                  @include transition();
                }
              }
            }
          }
        }
      }

      .right {
        width: 100%;
        box-sizing: border-box;

        @media #{$tablet-l} {
          width: 48%;
          padding-left: px-to-vw(140);
        }

        .slider-accordion {
          @include transition();
          position: relative;
          margin-bottom: max(px-to-vw(20), 10px);
          padding: 20px 25px;
          box-sizing: border-box;
          background-color: #fff;
          border-radius: max(px-to-vw(15), 10px);

          @media #{$tablet-s} {
            padding: max(px-to-vw(32), 18px) max(px-to-vw(40), 22px);
            padding-right: max(px-to-vw(23), 10px);
          }

          &:last-child {
            margin-bottom: 0;
          }

          &:hover,
          &.active {
            background-color: $yellow;

            .swiper-btn {
              background-color: #fff;

              svg {
                path,
                line {
                  stroke: $grey-blue;
                }
              }
            }
          }

          &.active {
            background-color: $yellow;

            .swiper-btn {
              transform: rotate(180deg);
            }
          }

          &:not(.active) {
            cursor: pointer;
          }

          .slider-accordion-header {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .slider-accordion-title {
              font-family: $font-medium;
              color: $grey-blue;
              font-size: 18px;
              display: flex;
              align-items: center;

              @media #{$tablet-s} {
                font-size: max(px-to-vw(24), 16px);
              }

              img {
                margin-right: max(px-to-vw(13), 12px);
                width: px-to-vw(32);
                min-width: 32px;
                height: auto;
                max-height: max(px-to-vw(25), 25px);
              }
            }

            .swiper-btn {
              position: relative;
              z-index: 2;

              svg {
                transform: rotate(90deg);
              }
            }
          }

          .slider-accordion-hidden-content {
            display: none;
            margin-top: 5px;

            @media #{$tablet-s} {
              margin-top: max(px-to-vw(13), 13px);
            }

            p {
              @include small-editor-styles();
              @include text-1();
              padding-right: 52px;

              @media #{$laptop-s} {
                padding-right: 0;
                width: px-to-vw(444);
              }
            }
          }
        }

        .swiper-navigation-buttons {
          .swiper-btn {
            margin-top: max(#{px-to-vw(30)}, 30px);
          }
        }
      }
    }

    .slider-video-popup {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10;
      display: none;

      .popup-content {
        position: relative;
        height: 100%;
        display: flex;
        flex-direction: column;

        .slider-video-popup-closing-area {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(#000, 97%);
          z-index: 0;
          cursor: pointer;
        }

        .close-slider-video-popup-btn {
          @include transition();
          position: absolute;
          top: max(px-to-vw(25), 25px);
          right: max(px-to-vw(45), 20px);
          width: max(px-to-vw(50), 40px);
          height: max(px-to-vw(50), 40px);
          border-radius: max(px-to-vw(50), 40px);
          background-color: #fff;
          z-index: 2;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;

          &:hover {
            background-color: $orange;

            svg {
              line {
                stroke: #fff;
              }
            }
          }

          svg {
            width: max(px-to-vw(13), 11px);
            height: auto;

            line {
              @include transition();
            }
          }
        }

        .video-holder {
          margin: auto;
          z-index: 1;
          width: 100%;

          @media #{$laptop-s} {
            width: px-to-vw(1297);
          }

          .play-slider-video-btn {
            @include transition();
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: max(px-to-vw(100), 55px);
            height: max(px-to-vw(100), 55px);
            border-radius: max(px-to-vw(100), 55px);
            z-index: 2;
            background-color: $yellow;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;

            &:hover {
              background-color: $orange;

              svg {
                path {
                  &:last-child {
                    fill: #fff;
                  }
                }
              }
            }

            svg {
              width: max(px-to-vw(27), 18px);
              height: auto;
              transform: translate(15%);

              path {
                @include transition();
              }
            }
          }

          .video {
            padding-top: 56.25%;
            position: relative;
            border-radius: max(px-to-vw(40), 20px);
            overflow: hidden;
            width: 100%;

            iframe {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background-color: $grey-blue;
              z-index: 1;
            }
          }
        }
      }
    }
  }

  .lists-section {
    background-color: $orange;
    padding: max(px-to-vw(85), 46px) 0 max(px-to-vw(85), 55px);

    .title {
      @include title-3();
      color: #fff;
      text-align: center;
      margin-bottom: max(px-to-vw(20), 14px);
    }

    .description {
      @include small-editor-styles();
      @include text-2();
      color: #fff;
      text-align: center;
    }

    .lists-blocks {
      margin-top: max(px-to-vw(60), 35px);

      .btn-icon {
        &.active {
          span {
            transform: rotate(180deg);
          }
        }

        &.desktop {
          display: none;

          @media #{$tablet-s} {
            display: flex;
          }
        }

        &.mobile {
          margin-top: 5px;

          @media #{$tablet-s} {
            display: none;
          }
        }

        &:hover {
          span {
            background-color: $orange;

            svg {
              path,
              line {
                stroke: #fff;
              }
            }
          }
        }
      }

      .list-block {
        @include transition();
        background-color: #fff;
        border-radius: max(px-to-vw(15), 10px);
        padding: max(px-to-vw(37), 25px) max(px-to-vw(70), 25px);
        box-sizing: border-box;
        margin-bottom: max(px-to-vw(38), 10px);

        &:last-child {
          margin-bottom: 0;
        }

        &.active {
          background-color: $yellow;
        }

        .list-block-header {
          border-bottom: solid 1px $grey-blue;
          padding-bottom: max(#{px-to-vw(30)}, 24px);
          margin-bottom: max(#{px-to-vw(38)}, 24px);
          display: flex;
          align-items: center;
          justify-content: space-between;

          .list-block-title {
            @include title-4();
            color: $grey-blue;
            display: flex;
            align-items: center;

            img {
              margin-right: max(#{px-to-vw(18)}, 10px);
              width: max(#{px-to-vw(41)}, 32px);
            }
          }
        }

        .list-items-desktop {
          display: none;
          flex-wrap: wrap;
          width: calc(100% + #{px-to-vw(90)});
          margin-left: px-to-vw(-45);

          @media #{$tablet-s} {
            display: flex;
          }

          .col {
            width: 33.333%;
            padding: 0 px-to-vw(45);
            box-sizing: border-box;

            .list-item {
              margin-bottom: max(#{px-to-vw(20)}, 15px);

              p {
                font-size: max(#{px-to-vw(24)}, 14px);
                font-family: $font-regular;
                color: $grey-blue;
                line-height: 1.25em;
              }
            }

            .hidden-items {
              display: none;
            }
          }
        }

        .list-items-mobile {
          @media #{$tablet-s} {
            display: none;
          }

          .list-item {
            margin-bottom: 15px;

            p {
              @include text-2();
            }

            &:last-child {
              margin-bottom: 0;
            }
          }

          .visible-items {
            padding-bottom: 15px;
          }

          .hidden-items {
            padding-bottom: 15px;
            display: none;
          }
        }
      }
    }
  }

  .image-with-description-section {
    background-color: $tan;
    padding: max(px-to-vw(100), 50px) 0 max(px-to-vw(100), 74px);

    .section-content {
      display: flex;
      flex-wrap: wrap;

      .left {
        width: 100%;
        margin-bottom: 38px;

        @media #{$tablet-s} {
          margin-bottom: 0;
          width: 55.7%;
        }

        .image-holder {
          position: relative;
          padding-top: 85.3%;
          background-color: $grey-blue;
          border-radius: max(px-to-vw(40), 20px);
          overflow: hidden;

          @media #{$tablet-s} {
            padding-top: 73.6%;
          }

          img {
            @include cropped-img();
          }
        }
      }

      .right {
        width: 100%;
        align-self: center;
        box-sizing: border-box;

        @media #{$tablet-s} {
          width: 44.3%;
          padding-left: px-to-vw(80);
        }

        @media #{$laptop-s} {
          padding-left: px-to-vw(260);
        }

        .title {
          @include title-3();
          color: $grey-blue;
        }

        .description {
          @include small-editor-styles();
          @include text-2();
          margin-top: max(px-to-vw(30), 14px);
        }

        .btn {
          margin: max(px-to-vw(45), 30px) 0 0;
        }
      }
    }
  }
}
